import {
  AfterViewInit,
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnDestroy,
  OnInit,
  Output,
  ViewChild,
} from '@angular/core';
import { Subscription } from 'rxjs';
import { ArticoloMySelf } from 'src/app/shared/models/dto';
import { ContainerService } from 'src/app/shared/services/container.service';

@Component({
  selector: 'app-my-self-extras',
  templateUrl: './my-self-extras.component.html',
  styleUrls: ['./my-self-extras.component.scss']
})
export class MySelfExtrasComponent implements OnInit, AfterViewInit, OnDestroy {

  @Input() articoli: ArticoloMySelf[] = [];
  @Input() numeroPax: number;

  @Output() quantitaGiorniChanged: EventEmitter<any> = new EventEmitter();

  @ViewChild('extrasContainer') extrasContainer: ElementRef;

  public extraIndex: number = 0;
  public changeEnabled: boolean = true;
  public cursorStartX: number;
  public cursorStartY: number;
  public cursorEndX: number;
  public cursorEndY: number;
  public width: number;
  public startEndScroll: number;
  public sub: Subscription;

  constructor(public containerService: ContainerService) { }

  ngOnInit() {
    this.sub = this.containerService.windowResizeEvent.subscribe(() => {
      setTimeout(() => {
        this.calculateWidth();
        this.toIndex(this.extraIndex);
      }, 500);
    });
  }

  ngAfterViewInit() {
    this.calculateWidth();
  }

  previous() {
    if (this.changeEnabled && this.extraIndex > 0) {
      this.changeEnabled = false;

      if (this.extraIndex == this.articoli.length - 1) {
        this.extrasContainer.nativeElement.scrollTo({ top: 0, left: this.extrasContainer.nativeElement.scrollLeft - this.startEndScroll, behavior: 'smooth' });
      } else {
        this.extrasContainer.nativeElement.scrollTo({ top: 0, left: this.extrasContainer.nativeElement.scrollLeft - this.width, behavior: 'smooth' });
      }

      setTimeout(() => {
        this.extraIndex--;
        this.changeEnabled = true;
      }, 500);
    }
  }

  next() {
    if (this.changeEnabled && this.extraIndex < this.articoli.length - 1) {
      this.changeEnabled = false;

      if (this.extraIndex == 0) {
        this.extrasContainer.nativeElement.scrollTo({ top: 0, left: this.extrasContainer.nativeElement.scrollLeft + this.startEndScroll, behavior: 'smooth' });
      } else {
        this.extrasContainer.nativeElement.scrollTo({ top: 0, left: this.extrasContainer.nativeElement.scrollLeft + this.width, behavior: 'smooth' });
      }

      setTimeout(() => {
        this.extraIndex++;
        this.changeEnabled = true;
      }, 500);
    }
  }

  toIndex(index: number) {
    if (this.changeEnabled) {
      this.changeEnabled = false;
      this.extrasContainer.nativeElement.scrollTo({ top: 0, left: index >= 1 ? this.startEndScroll + (this.width * (index - 1)) : 0, behavior: 'smooth' });

      setTimeout(() => {
        this.extraIndex = index;
        this.changeEnabled = true;
      }, 500);
    }
  }

  checkGesture() {
    if (this.articoli.length > 1) {
      if (Math.abs(this.cursorStartX - this.cursorEndX) > 50 && Math.abs(this.cursorStartY - this.cursorEndY) < 50) {
        if (this.cursorStartX < this.cursorEndX) {
          this.previous();
        } else {
          this.next();
        }
      }
    }
  }

  calculateWidth() {
    this.width = (this.extrasContainer.nativeElement.offsetWidth * 70 / 100) + 16;
    this.startEndScroll = this.width - (this.extrasContainer.nativeElement.offsetWidth * 15 / 100) + 24;
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }
}