import { Component, EventEmitter, Input, Output } from '@angular/core';
import { SessionService } from 'src/app/core/session.service';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { Cliente, Lingua } from 'src/app/shared/models/bo.model';
import {
  DialogOptions,
  GetNazioneTradottaRQ,
  PortaleMySelf,
  RecensioneMySelf,
  WebApiResponse,
} from 'src/app/shared/models/dto';
import { ListaSelfCheckIn } from 'src/app/shared/models/enum';
import { ApiService } from 'src/app/shared/services/api.service';
import { ContainerService } from 'src/app/shared/services/container.service';
import { LocalizationService } from 'src/app/shared/services/localization.service';
import { UtilityService } from 'src/app/shared/services/utility.service';

import {
  MySelfNuovaRecensioneComponent,
} from '../../my-self-shared/my-self-nuova-recensione/my-self-nuova-recensione.component';
import { MySelfDatiEditComponent } from '../my-self-dati-edit/my-self-dati-edit.component';

@Component({
  selector: 'app-my-self-header',
  templateUrl: './my-self-header.component.html',
  styleUrls: ['./my-self-header.component.scss']
})
export class MySelfHeaderComponent extends BaseComponent {

  @Input() portale: PortaleMySelf;
  @Input() lingua: string;
  @Input() selectedLanguage: Lingua;
  @Input() lingueAttive: Lingua[];
  @Input() cliente: Cliente;
  @Input() uid: string;
  @Input() listaVisible: ListaSelfCheckIn;

  @Output() onMenuClick: EventEmitter<ListaSelfCheckIn> = new EventEmitter();
  @Output() onRecensioneAdd: EventEmitter<RecensioneMySelf> = new EventEmitter();

  public enumListaSelfCheckIn = ListaSelfCheckIn;

  constructor(private apiService: ApiService,
    private sessionService: SessionService,
    public utility: UtilityService,
    private containerService: ContainerService,
    private localizationService: LocalizationService) {
    super();
  }

  nuovaRecensione() {
    let dialogOptions = new DialogOptions();

    if (this.containerService.dimension.width >= 992) {
      dialogOptions.width = "50vw";
    } else {
      dialogOptions.width = "80vw";
    }

    dialogOptions.panelClass = 'myself-dialog';

    let ref = this.showModalDialog(MySelfNuovaRecensioneComponent, dialogOptions);
    let component = <MySelfNuovaRecensioneComponent>ref.componentInstance;
    component.cliente = this.cliente;
    component.portale = this.portale;
    component.dialogRef = ref;

    ref.afterClosed().subscribe(x => {
      if (x) {
        this.onRecensioneAdd.emit(x);
      }
    });
  }

  onClienteEdit() {
    let clienteModel = JSON.parse(JSON.stringify(this.cliente));

    if (!!clienteModel.nazione) {
      this.setIsLoading(true);

      let rq = new GetNazioneTradottaRQ();
      rq.nomeItaliano = clienteModel.nazione;
      rq.lingua = this.lingua;

      this.apiService.getNazioneTradotta(rq).subscribe((res: WebApiResponse) => {
        if (res.data != null) {
          clienteModel.nazioneObject = res.data;
        }

        this.setIsLoading(false);

        this.openClienteEditModal(clienteModel);
      });
    } else {
      this.openClienteEditModal(clienteModel);
    }
  }

  openClienteEditModal(clienteModel: Cliente) {
    let dialogOptions = new DialogOptions();

    if (this.containerService.dimension.width >= 992) {
      dialogOptions.width = "50vw";
    } else {
      dialogOptions.width = "80vw";
    }

    dialogOptions.panelClass = 'myself-dialog';

    let ref = this.showModalDialog(MySelfDatiEditComponent, dialogOptions);
    let component = <MySelfDatiEditComponent>ref.componentInstance;
    component.clienteModel = clienteModel;
    component.booking = !!this.uid;
    component.lingua = this.lingua;
    component.dialogRef = ref;

    ref.afterClosed().subscribe((x: Cliente) => {
      if (x) {
        this.cliente = x;
      }
    });
  }

  logout() {
    this.showDialogConfirm('myself.effettuareLogout')
      .then(si => {
        this.setIsLoading(true);
        let oidAlbergo = this.sessionService.getOidAlbergo();
        this.sessionService.clearSessione();
        window.location.href = '/self-checkin?oidAlbergo=' + oidAlbergo + '&lingua=' + this.localizationService.currentLanguage.codice;
      },
        no => { }
      );
  }
}