import { Component, Input, OnInit } from '@angular/core';
import { MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { LocalStorageService } from 'src/app/core/local-storage.service';
import { SessionService } from 'src/app/core/session.service';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { Cliente } from 'src/app/shared/models/bo.model';
import { GetNazioniTradotteRQ, WebApiResponse } from 'src/app/shared/models/dto';
import { ApiService } from 'src/app/shared/services/api.service';

@Component({
  selector: 'app-my-self-dati-edit',
  templateUrl: './my-self-dati-edit.component.html',
  styleUrls: ['./my-self-dati-edit.component.scss']
})
export class MySelfDatiEditComponent extends BaseComponent implements OnInit {

  @Input() clienteModel: Cliente;
  @Input() booking: boolean;
  @Input() lingua: string;
  @Input() dialogRef: MatDialogRef<{}, any>;

  public getNazioniTradotteRQ = new GetNazioniTradotteRQ();

  constructor(public apiService: ApiService,
    private sessionService: SessionService,
    private localStorage: LocalStorageService) {
    super();
  }

  ngOnInit() {
    this.getNazioniTradotteRQ.addEmptyItem = true;
    this.getNazioniTradotteRQ.lingua = this.lingua;
  }

  saveCliente() {
    this.setIsLoading(true);

    if (this.clienteModel.password == null) {
      this.clienteModel.password = '';
    }

    this.apiService.setCliente(this.clienteModel).subscribe(() => {
      if (this.clienteModel.nazioneObject != null) {
        this.clienteModel.nazione = this.clienteModel.nazioneObject.nomeItaliano;
      }

      if (!this.booking) {
        this.sessionService.sessione.clienteMySelf.nome = this.clienteModel.nome;
        this.sessionService.sessione.clienteMySelf.cognome = this.clienteModel.cognome;
        this.sessionService.sessione.clienteMySelf.email = this.clienteModel.email;
        this.sessionService.sessione.clienteMySelf.cellulare = this.clienteModel.cellulare;
        this.sessionService.sessione.clienteMySelf.password = this.clienteModel.password;
        this.sessionService.sessione.clienteMySelf.indirizzo = this.clienteModel.indirizzo;
        this.sessionService.sessione.clienteMySelf.nazione = this.clienteModel.nazioneObject;
        this.sessionService.sessione.clienteMySelf.nazioneString = this.clienteModel.nazione;
        this.sessionService.sessione.clienteMySelf.lingua = this.clienteModel.lingua != null ? this.clienteModel.lingua.codice : '';

        if (this.clienteModel.nazioneObject != null && this.clienteModel.nazioneObject.oid > 0) {
          this.apiService.getTraduzioniNazione(this.clienteModel.nazioneObject.oid).subscribe((rs: WebApiResponse) => {
            this.sessionService.sessione.clienteMySelf.nazioniXLingua = rs.data;
            this.localStorage.set(this.sessionService.SESSION_KEY, this.sessionService.sessione);
          });
        } else {
          this.localStorage.set(this.sessionService.SESSION_KEY, this.sessionService.sessione);
        }
      }

      this.setIsLoading(false);

      this.showInfo('globals.messages.datiSalvati').then(() => {
        this.dialogRef.close(this.clienteModel);
      });
    });
  }
}