import { Component, ElementRef, Input, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { DeviceDetectorService } from 'ngx-device-detector';
import { Subscription } from 'rxjs';
import { SessionService } from 'src/app/core/session.service';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { Cliente, Lingua } from 'src/app/shared/models/bo.model';
import {
  AlbergoMySelf,
  ContenutoMySelf,
  Dimension,
  GetMySelfBookingRQ,
  GetMySelfBookingRS,
  GetMySelfRQ,
  GetMySelfRS,
  MessaggioMySelf,
  PortaleMySelf,
  PrenotazioneMySelf,
  RecensioneMySelf,
  RecuperaPasswordRQ,
  RecuperaPasswordRS,
  RichiestaMySelf,
  VoucherMySelf,
} from 'src/app/shared/models/dto';
import { ListaSelfCheckIn, MittenteMessaggio, Pagina } from 'src/app/shared/models/enum';
import { ApiService } from 'src/app/shared/services/api.service';
import { ContainerService } from 'src/app/shared/services/container.service';
import { CssLoaderService } from 'src/app/shared/services/css-loader.service';
import { LocalizationService } from 'src/app/shared/services/localization.service';
import { UtilityService } from 'src/app/shared/services/utility.service';

@Component({
  selector: 'app-my-self',
  templateUrl: './my-self.component.html',
  styleUrls: ['./my-self.component.scss']
})
export class MySelfComponent extends BaseComponent implements OnInit, OnDestroy {

  @Input() uid: string;

  public portale: PortaleMySelf = new PortaleMySelf();
  public initialized: boolean = false;
  public prenotazioni: PrenotazioneMySelf[] = [];
  public richieste: RichiestaMySelf[] = [];
  public voucher: VoucherMySelf[] = [];
  public recensioni: RecensioneMySelf[] = [];
  public messaggi: MessaggioMySelf[] = [];
  public cliente: Cliente;
  public all: boolean = false;
  public albergo: AlbergoMySelf = new AlbergoMySelf();
  public enumMittente = MittenteMessaggio;
  public listaVisible: ListaSelfCheckIn = ListaSelfCheckIn.Prenotazioni;
  public enumListaSelfCheckIn = ListaSelfCheckIn;
  public sub: Subscription;
  public sub1: Subscription;
  public lingua: string;
  public selectedLanguage: Lingua = new Lingua();
  public lingueAttive: Lingua[] = [];
  public traduzioni: string;
  public password: string;
  public passwordInserted: boolean = true;
  public contenuti: ContenutoMySelf[] = [];
  public recuperoPassword: boolean = false;
  public oidPortale: number;
  public condizioniPrenotazione: string;
  public tabIndex: number = 0;
  public backgroundImage: string;
  public isReadOnlyMode: boolean = false;

  @ViewChild('myselfcol') myselfcol: ElementRef;

  constructor(public apiService: ApiService,
    public containerService: ContainerService,
    private deviceService: DeviceDetectorService,
    private sessionService: SessionService,
    private localizationService: LocalizationService,
    private cssLoaderService: CssLoaderService,
    private utility: UtilityService,
    private route: ActivatedRoute,
    private router: Router) {
    super();
  }

  ngOnInit() {
    this.containerService.setScrollingContainer('my-self-scrolling');

    if (!this.uid) {
      this.sessionService.isAttachRequestHeaders = true;
    }

    this.sub1 = this.route.queryParamMap.subscribe(x => {
      let oidPortale = x.get('oidPortale');

      if (!!oidPortale && !isNaN(+oidPortale)) {
        this.oidPortale = +oidPortale;
      }

      this.download(x.get('lingua')).then(() => {
        this.containerService.setColor(this.portale.coloreSelfCheckIn);
        this.cssLoaderService.setColor(this.portale.coloreSelfCheckIn);

        this.utility.setLingua(this.selectedLanguage?.codice).then((lingua: Lingua) => {
          this.lingua = lingua?.codice;

          if (!!this.traduzioni) {
            let json;

            try {
              json = JSON.parse(this.traduzioni);
            } catch (e) { }

            if (!!json) {
              this.localizationService.setTranslation(this.localizationService.getCurrentLocale(), json);
            }
          }

          this.utility.setBackgroundImage(this.portale.urlImmagineSfondo).then((image: string) => {
            this.backgroundImage = image;
            this.initialized = true;
            this.setIsLoading(false);
          });
        });
      });
    });

    this.sub = this.containerService.windowResizeEvent.subscribe((d: Dimension) => {
      if (d.width >= 992) {
        this.tabIndex = 0;
      }

      this.updateScroll();
    });
  }

  download(lingua?: string) {
    this.setIsLoading(true);

    let promise = new Promise<void>((resolve) => {
      if (!!this.uid) {
        let rq = new GetMySelfBookingRQ();
        rq.uid = this.uid;
        rq.isMobile = this.deviceService.isMobile();
        rq.oidPortale = this.oidPortale;
        rq.lingua = !!lingua ? lingua : this.lingua;

        this.apiService.getMySelfBooking(rq).subscribe((res: GetMySelfBookingRS) => {
          if (res.data.prenotazioni != null && res.data.prenotazioni.length > 0 && res.data.albergo != null) {
            let isPrenotazioneEliminata = res.data.prenotazioni.some(x => x.isEliminata);

            if (!isPrenotazioneEliminata || res.data.portale?.isVisualizzaPrenotazioneEliminata) {
              this.isReadOnlyMode = isPrenotazioneEliminata;

              this.albergo = res.data.albergo;
              this.cssLoaderService.load(this.albergo.oid, Pagina.MySelf, res.data.portale?.oidUbicazioneIstat);

              if (res.data.portale != null) {
                this.portale = res.data.portale;
              }

              this.prenotazioni = res.data.prenotazioni;
              this.messaggi = res.data.messaggi;
              this.cliente = res.data.prenotazioni[0].cliente;
              this.lingueAttive = res.data.lingue;
              this.selectedLanguage = res.data.lingua;
              this.traduzioni = res.data.traduzioni;
              this.password = res.data.password;
              this.passwordInserted = !this.password;
              this.contenuti = res.data.contenuti;
              this.condizioniPrenotazione = res.data.condizioniPrenotazione;

              resolve();
            } else {
              let queryParams: any = {};

              if (res.data.portale?.oid > 0) {
                queryParams.oidPortale = res.data.portale.oid;
              } else {
                queryParams.oidAlbergo = res.data.albergo.oid;
              }

              if (!!res.data.lingua?.codice) {
                queryParams.lingua = res.data.lingua.codice;
              } else {
                queryParams.lingua = 'it';
              }

              this.router.navigate(['booking'], { queryParams: queryParams }).then(() => {
                this.setIsLoading(false);
              });
            }
          } else {
            this.router.navigate(['sbm']).then(() => {
              this.setIsLoading(false);
            });
          }
        });
      } else {
        let rq = new GetMySelfRQ();

        if (this.sessionService.isLoggedInMySelf()) {
          this.albergo = this.sessionService.getAlbergo();

          rq.oidAlbergo = this.albergo.oid;
          rq.oidCliente = this.sessionService.getOidAnagraficaHotel();
          rq.all = this.all;
          rq.isMobile = this.deviceService.isMobile();
          rq.oidPortale = this.oidPortale;
          rq.lingua = lingua;
          rq.isMySelf = false;

          this.apiService.getMySelf(rq).subscribe((res: GetMySelfRS) => {
            this.cssLoaderService.load(this.albergo.oid, Pagina.MySelf, res.data.portale?.oidUbicazioneIstat);

            if (res.data.portale != null) {
              this.portale = res.data.portale;
            }

            this.prenotazioni = res.data.prenotazioni;
            this.richieste = res.data.richieste;
            this.voucher = res.data.voucher;
            this.recensioni = res.data.recensioni;
            this.messaggi = res.data.messaggi;
            this.cliente = res.data.cliente;
            this.lingueAttive = res.data.lingue;
            this.selectedLanguage = res.data.lingua;
            this.traduzioni = res.data.traduzioni;
            this.contenuti = res.data.contenuti;
            this.condizioniPrenotazione = res.data.condizioniPrenotazione;

            resolve();
          });
        } else {
          this.router.navigate(['sbm']).then(() => {
            this.setIsLoading(false);
          });
        }
      }
    });

    return promise;
  }

  onAllChange() {
    this.download().then(() => {
      this.containerService.scrollToTop();
      this.setIsLoading(false);
    });
  }

  checkPassword(password: string) {
    if (password == this.password) {
      this.passwordInserted = true;
    } else {
      this.showError('myself.passwordErrata');
    }
  }

  recuperaPassword(email: string) {
    if (!!email) {
      this.setIsLoading(true);

      let rq = new RecuperaPasswordRQ();
      rq.oidCliente = this.cliente.oid;
      rq.oidAlbergo = this.albergo.oid;
      rq.oidPortale = this.portale.oid;

      this.apiService.recuperaPassword(rq).subscribe((res: RecuperaPasswordRS) => {
        this.setIsLoading(false);

        if (res.data.success) {
          this.showInfo('myself.emailInviata').then(() => {
            this.recuperoPassword = false;
          });
        } else {
          this.showError('myself.erroreRecuperoPassword').then(() => {
            this.recuperoPassword = false;
          });
        }
      });
    } else {
      this.showError('globals.messages.inserireDati');
    }
  }

  openBooking() {
    if (this.portale.oid != null && this.portale.oid > 0) {
      this.utility.openUrlInNewTab(window.location.origin + '/booking?oidPortale=' + this.portale.oid);
    } else {
      this.utility.openUrlInNewTab(window.location.origin + '/booking?oidAlbergo=' + this.albergo.oid);
    }
  }

  onTabChange(index: number) {
    this.tabIndex = index;
    this.updateScroll();
    this.containerService.scrollToTop();

    /*if (index == 1) {
      if (!!this.uid) {
        this.prenotazioni.forEach(x => this.apiService.impostaMessaggiLettiHotelPrenotazione(x.oid).subscribe());
      } else {
        this.apiService.impostaMessaggiLettiHotelCliente(this.cliente.oid).subscribe();
      }
    }*/
  }

  updateScroll() {
    if (this.myselfcol != null) {
      this.myselfcol.nativeElement.scrollTo({ top: 0, left: this.tabIndex * this.myselfcol.nativeElement.offsetWidth, behavior: 'smooth' });
    }
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
    this.sub1.unsubscribe();
  }
}