import { Component, Input } from '@angular/core';
import { MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { RichiediCancellazionePrenotazioneRQ, RichiediCancellazionePrenotazioneRS } from 'src/app/shared/models/dto';
import { ApiService } from 'src/app/shared/services/api.service';

@Component({
  selector: 'app-my-self-richiedi-cancellazione-dialog',
  templateUrl: './my-self-richiedi-cancellazione-dialog.component.html',
  styleUrls: ['./my-self-richiedi-cancellazione-dialog.component.scss']
})
export class MySelfRichiediCancellazioneDialogComponent extends BaseComponent {

  @Input() oidPrenotazione: number;
  @Input() dialogRef: MatDialogRef<{}, any>;

  public note: string;

  constructor(private apiService: ApiService) {
    super();
  }

  richiediCancellazione() {
    this.setIsLoading(true);

    let rq = new RichiediCancellazionePrenotazioneRQ();
    rq.oidPrenotazione = this.oidPrenotazione;
    rq.noteCancellazione = this.note;

    this.apiService.richiediCancellazionePrenotazione(rq).subscribe((res: RichiediCancellazionePrenotazioneRS) => {
      this.setIsLoading(false);

      if (res.data.success) {
        this.showInfo('myself.emailInviata').then(() => {
          this.dialogRef.close(true);
        });
      } else {
        this.showInfo('myself.erroreInvioEmail').then(() => {
          this.dialogRef.close();
        });
      }
    });
  }
}
