import { Component, ElementRef, Input, OnInit, ViewChild } from '@angular/core';
import * as moment from 'moment';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import {
  TipoCameraTariffaDetailsDialogComponent,
} from 'src/app/interfaces/tipo-camera-tariffa-details-dialog/tipo-camera-tariffa-details-dialog.component';
import { ComponenteXPrenotazione } from 'src/app/shared/models/bo.model';
import {
  AlbergoMySelf,
  ArticoloMySelf,
  BookingSolutionRoomRS,
  CreaPrenotazioneExtraRQ,
  CreaPrenotazioneExtraRS,
  DialogOptions,
  PrenotazioneMySelf,
  PrenotazioneMySelfDettaglio,
  ReplaceTagsTemplateRQ,
  ReplaceTagsTemplateRS,
  TariffaMySelf,
  TipoCameraMySelf,
} from 'src/app/shared/models/dto';
import { StatoPrenotazione, TipoModalitaPagamento } from 'src/app/shared/models/enum';
import { ApiService } from 'src/app/shared/services/api.service';
import { ContainerService } from 'src/app/shared/services/container.service';
import { LocalizationService } from 'src/app/shared/services/localization.service';
import { UtilityService } from 'src/app/shared/services/utility.service';

import { BonificoDetailsModalComponent } from '../../interfaces/bonifico-details-modal/bonifico-details-modal.component';
import {
  MySelfCondizioniPrenotazioneDialogComponent,
} from './my-self-condizioni-prenotazione-dialog/my-self-condizioni-prenotazione-dialog.component';
import {
  MySelfRichiediCancellazioneDialogComponent,
} from './my-self-richiedi-cancellazione-dialog/my-self-richiedi-cancellazione-dialog.component';

@Component({
  selector: 'app-my-self-prenotazione',
  templateUrl: './my-self-prenotazione.component.html',
  styleUrls: ['./my-self-prenotazione.component.scss']
})
export class MySelfPrenotazioneComponent extends BaseComponent implements OnInit {

  @Input() set _prenotazione(value: PrenotazioneMySelf) {
    if (value != null) {
      this.prenotazione = value;

      if (moment(this.getDate(this.prenotazione.arrivo)).isBefore(moment().startOf('day'))) {
        this.buttonsDisabled = true;
      }

      this.tipoCamera = value.tipoCamera;
      this.tariffa = value.tariffa;
      this.articoli = value.articoli;
      this.numeroAdulti = value.numeroAdulti;
      this.numeroBambini = value.numeroBambini;
      this.etaBambini = value.etaBambini;
      this.isSondaggioCompilato = value.isSondaggioCompilato;
      this.ospiti = value.ospiti;
      this.camere = value.upSellingRooms;
      this.dettagli = value.dettagli;

      if (!!value.dataUpSelling && !!value.cameraUpSelling) {
        this.upSellingRichiesto = this.localizationService.getTranslationSync('myself.upSellingRichiesto', { data: this.getDate(value.dataUpSelling, 'DD/MM/YYYY'), camera: value.cameraUpSelling });
      }

      if (this.prenotazione != null && !!this.prenotazione.arrivo && !!this.prenotazione.partenza) {
        this.notti = Math.max(moment(this.getDate(this.prenotazione.partenza)).diff(moment(this.getDate(this.prenotazione.arrivo)), 'days'), 1);
      }
    }
  };

  @Input() isAttivaBookingExtraSelfCheckIn: boolean;
  @Input() isAttivaCancellazioneSelfCheckIn: boolean;
  @Input() isAttivaUpSellingSelfCheckIn: boolean;
  @Input() isVisualizzaTuttiDatiSelfCheckIn: boolean;
  @Input() isBloccaModificaDatiCheckIn: boolean;
  @Input() condizioniPrenotazione: string;
  @Input() albergo: AlbergoMySelf;
  @Input() templateStampaOid: number;
  @Input() lingua: string;
  @Input() orarioDiArrivoMinimo: string;
  @Input() orarioDiArrivoMassimo: string;
  @Input() isReadOnlyMode: boolean = false;

  @ViewChild('articoliContainer') articoliContainer: ElementRef;
  @ViewChild('checkInContainer') checkInContainer: ElementRef;
  @ViewChild('prenotazioneContainer') prenotazioneContainer: ElementRef;

  public prenotazione: PrenotazioneMySelf;
  public tipoCamera: TipoCameraMySelf;
  public tariffa: TariffaMySelf;
  public articoli: ArticoloMySelf[] = [];
  public articoliSelected: ArticoloMySelf[] = [];
  public ospiti: ComponenteXPrenotazione[];
  public notti: number = 0;
  public numeroAdulti: number = 0;
  public numeroBambini: number = 0;
  public etaBambini: string;
  public isSondaggioCompilato: boolean;
  public articoliVisible: boolean = false;
  public checkInVisible: boolean = false;
  public ospitiCheckIn: ComponenteXPrenotazione[];
  public print: boolean = false;
  public buttonsDisabled: boolean = false;
  public camere: BookingSolutionRoomRS[];
  public upSellingRichiesto: string;
  public dettagli: PrenotazioneMySelfDettaglio[];
  public enumTipoModalitaPagamento = TipoModalitaPagamento;
  public enumStatoPrenotazione = StatoPrenotazione;
  public templateStampaHtml: string;
  public checkInIndex: number = 0;

  constructor(private utility: UtilityService,
    private apiService: ApiService,
    public containerService: ContainerService,
    private localizationService: LocalizationService) {
    super();
  }

  ngOnInit() {
    if (this.isReadOnlyMode) {
      this.buttonsDisabled = true;
    }
  }

  openDetails() {
    let dialogOptions = new DialogOptions();

    if (this.containerService.dimension.width >= 992) {
      dialogOptions.width = "50vw";
    } else {
      dialogOptions.width = "80vw";
    }

    dialogOptions.panelClass = 'dialog-overlay';

    let ref = this.showModalDialog(TipoCameraTariffaDetailsDialogComponent, dialogOptions);
    let component = <TipoCameraTariffaDetailsDialogComponent>ref.componentInstance;
    component.tipoCamera = this.tipoCamera;
    component.tariffa = this.tariffa;
    component.isVisualizzaDettaglioGiornaliero = false;
  }

  openSondaggio() {
    this.utility.openUrlInNewTab(window.location.origin + '/booking/sondaggio?oidPrenotazione=' + this.prenotazione.oid);
  }

  getSaldo() {
    if (this.prenotazione != null) {
      return this.prenotazione.importo - this.prenotazione.sconto - this.prenotazione.scontoVoucher - this.prenotazione.caparra;
    }

    return 0;
  }

  richiediCancellazione() {
    let dialogOptions = new DialogOptions();

    if (this.containerService.dimension.width >= 992) {
      dialogOptions.width = "50vw";
    } else {
      dialogOptions.width = "80vw";
    }

    dialogOptions.panelClass = 'myself-dialog';

    let ref = this.showModalDialog(MySelfRichiediCancellazioneDialogComponent, dialogOptions);
    let component = <MySelfRichiediCancellazioneDialogComponent>ref.componentInstance;
    component.oidPrenotazione = this.prenotazione.oid;
    component.dialogRef = ref;

    ref.afterClosed().subscribe(x => {
      if (x) {
        this.prenotazione.statoPrenotazione = StatoPrenotazione.CancellazioneRichiesta;
      }
    });
  }

  creaPrenotazioneExtra() {
    this.setIsLoading(true);

    let rq = new CreaPrenotazioneExtraRQ();
    rq.oidPrenotazione = this.prenotazione.oid;
    rq.articoli = this.articoli;
    rq.isUpSelling = true;

    this.apiService.creaPrenotazioneExtraSelfCheckin(rq).subscribe((res: CreaPrenotazioneExtraRS) => {
      if (res.data.success) {
        this.resetArticoli();
        this.showInfo('myself.richiestaInviata');
      } else {
        this.showError('myself.erroreRichiesta');
      }

      this.setIsLoading(false);
    });
  }

  resetArticoli() {
    this.articoli.forEach(x => {
      x.quantita = 0;

      if (x.giorni != null) {
        x.giorni.forEach(y => y.selected = true);
      }
    });

    this.updateArticoliSelected();
  }

  updateArticoliSelected() {
    this.articoliSelected = this.articoli.filter(x => x.quantita > 0);
    this.articoliSelected.forEach(x => x.totale = x.quantita * (x.giorni != null ? x.giorni.filter(y => y.selected).reduce((sum, current) => sum + current.prezzo, 0) : x.prezzo));
  }

  checkArticoliSelected(): boolean {
    return this.articoliSelected.some(x => x.quantita > 0);
  }

  onAggiungiServiziClick() {
    this.articoliVisible = !this.articoliVisible;

    if (this.articoliVisible) {
      setTimeout(() => {
        this.articoliContainer.nativeElement.scrollIntoView({ behavior: 'smooth' });
      }, 200);
    }
  }

  onCheckInClick(index: number) {
    if (!this.checkInVisible) {
      this.ospitiCheckIn = JSON.parse(JSON.stringify(this.ospiti));

      this.checkInIndex = index;

      this.checkInVisible = !this.checkInVisible;

      setTimeout(() => {
        this.checkInContainer.nativeElement.scrollIntoView({ behavior: 'smooth' });
      }, 200);
    } else {
      this.checkInVisible = !this.checkInVisible;
      this.prenotazioneContainer.nativeElement.scrollIntoView({ behavior: 'smooth' });
    }
  }

  onCheckInBack(ospiti: ComponenteXPrenotazione[]) {
    this.ospiti = ospiti;
  }

  onStepChange() {
    this.checkInContainer.nativeElement.scrollIntoView({ behavior: 'smooth' });
  }

  stampa() {
    if (this.templateStampaOid == null) {
      this.print = true;

      setTimeout(() => {
        window.print();

        setTimeout(() => {
          this.print = false;
        }, 1000);
      }, 200);
    } else {
      this.setIsLoading(true);

      let rq = new ReplaceTagsTemplateRQ();
      rq.oidTemplate = this.templateStampaOid;
      rq.oidPrenotazioni = [this.prenotazione.oid];

      this.apiService.replaceTagsTemplate(rq).subscribe((rs: ReplaceTagsTemplateRS) => {
        this.templateStampaHtml = rs.data.testo;
        this.setIsLoading(false);

        setTimeout(() => {
          window.print();
        }, 200);
      });
    }
  }

  openCondizioniPrenotazione() {
    let dialogOptions = new DialogOptions();

    if (this.containerService.dimension.width >= 992) {
      dialogOptions.width = "50vw";
    } else {
      dialogOptions.width = "80vw";
    }

    let ref = this.showModalDialog(MySelfCondizioniPrenotazioneDialogComponent, dialogOptions);
    let component = <MySelfCondizioniPrenotazioneDialogComponent>ref.componentInstance;
    component.condizioniPrenotazione = this.condizioniPrenotazione;
  }

  onRichiedi(camera: string) {
    this.prenotazioneContainer.nativeElement.scrollIntoView({ behavior: 'smooth' });
    this.upSellingRichiesto = this.localizationService.getTranslationSync('myself.upSellingRichiesto', { data: this.getDate(moment(), 'DD/MM/YYYY'), camera: camera });
    this.camere = null;
  }

  openBonificoDetails() {
    let dialogOptions = new DialogOptions();

    if (this.containerService.dimension.width >= 992) {
      dialogOptions.width = "50vw";
    } else {
      dialogOptions.width = "80vw";
    }

    dialogOptions.panelClass = 'myself-dialog';

    let ref = this.showModalDialog(BonificoDetailsModalComponent, dialogOptions);
    let component = <BonificoDetailsModalComponent>ref.componentInstance;
    component.modalitaPagamento = this.prenotazione.modalitaPagamento;
  }
}