import { Component, Input, OnInit } from '@angular/core';
import { PrenotazioneMySelfDettaglio } from 'src/app/shared/models/dto';
import { UtilityService } from 'src/app/shared/services/utility.service';

@Component({
  selector: 'app-my-self-prenotazione-summary',
  templateUrl: './my-self-prenotazione-summary.component.html',
  styleUrls: ['./my-self-prenotazione-summary.component.scss']
})
export class MySelfPrenotazioneSummaryComponent implements OnInit {

  @Input() tipoCamera: string;
  @Input() tariffa: string;
  @Input() promozione: string;
  @Input() sconto: number;

  @Input() set _dettagli(value: PrenotazioneMySelfDettaglio[]) {
    if (value != null) {
      this.dettagli = value;
      this.totale = this.dettagli.reduce((sum, current) => current.totale > 0 ? (sum + current.totale) : sum, 0);
    }
  };

  public dettagli: PrenotazioneMySelfDettaglio[];
  public totale: number = 0;
  public totaleScontato: number = 0;

  constructor(public utility: UtilityService) { }

  ngOnInit() {
    if (this.sconto > 0) {
      this.totaleScontato = this.totale - this.sconto;
    }
  }
}